@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-500.woff2") format("woff2"), url("../fonts/montserrat-500.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 62.5%;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  font-size: 1.6rem;
  background-color: #f9fbfd;
  position: relative;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.background-image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  top: 0;
  left: 0;
  background-color: #add8e6;
}

.header {
  position: absolute;
  width: 100%;
  z-index: 11;
  padding-top: 35px;
}
@media (max-width: 1365px) {
  .header {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.header__navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header__logo-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (max-width: 767px) {
  .header__logo-container {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.header__logo-link {
  color: #f9fbfd;
  display: flex;
  height: 38px;
  width: 70px;
  min-width: 70px;
}
@media (max-width: 1365px) {
  .header__logo-link {
    height: 20px;
    width: 38px;
    min-width: 38px;
  }
}
@media (max-width: 767px) {
  .header__logo-link {
    height: 22px;
    width: 42px;
    min-width: 42px;
  }
}
@media (hover: hover) {
  .header__logo-link:hover:not(.focus-visible) svg {
    opacity: 0.7;
  }
}
.header__logo-link.focus-visible:focus svg {
  opacity: 0.7;
}

.header__logo-link svg {
  width: 100%;
  height: 100%;
  transition: opacity 0.3s ease;
}

.header__links-open {
  display: none;
  background: transparent;
  border: none;
  width: 24px;
  height: 14px;
  color: #f9fbfd;
  padding: 0;
}
@media (max-width: 767px) {
  .header__links-open {
    display: block;
  }
}

.header__links-close {
  display: flex;
  align-items: center;
  background: transparent;
  padding: 0;
  border: none;
  margin-top: 5px;
}

.header__links-logo {
  color: #011c40;
  display: flex;
}

.header__links-top-menu {
  display: none;
}
@media (max-width: 767px) {
  .header__links-top-menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 33px;
  }
}

.header__links-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 767px) {
  .header__links-container {
    display: none;
    position: absolute;
    justify-content: start;
    background-color: #f9fbfd;
    height: 100vh;
    top: 0;
    z-index: 12;
    width: 89vw;
    right: -89vw;
    transition: right 0.3s ease;
    padding: 20px 15px 0 15px;
  }
}

@media (max-width: 767px) {
  .header__links-container--opening {
    display: flex;
  }
}

@media (max-width: 767px) {
  .header__links-container--open {
    display: flex;
    right: 0;
  }
}

.header__links-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style-type: none;
  color: #f9fbfd;
  font-weight: 500;
  gap: 33px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
}
@media (max-width: 1365px) {
  .header__links-list {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .header__links-list {
    flex-direction: column;
    gap: 10px;
    margin-left: -15px;
    margin-right: -15px;
  }
}

.header__link {
  color: #f9fbfd;
  display: flex;
  line-height: 2rem;
  min-height: 38px;
  align-items: center;
  transition: opacity 0.3s ease;
}
@media (max-width: 1365px) {
  .header__link {
    font-size: 1.4rem;
    line-height: 1.7rem;
    min-height: 20px;
  }
}
@media (max-width: 767px) {
  .header__link {
    color: #011c40;
    font-size: 1.8rem;
    line-height: 2.2rem;
    width: 100%;
    padding: 10px 30px 8px 25px;
  }
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__link.focus-visible:focus {
  opacity: 0.7;
}

.advantages {
  padding-top: 110px;
  padding-bottom: 120px;
  position: relative;
}
@media (max-width: 1365px) {
  .advantages {
    padding-top: 90px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 56px;
    padding-bottom: 65px;
  }
}

.advantages__background {
  background-color: #add8e6;
}

.advantages__title {
  text-align: center;
  margin-top: 0;
  margin-bottom: 50px;
  font-weight: 600;
  font-size: 2.8rem;
  line-height: 4.2rem;
}
@media (max-width: 1365px) {
  .advantages__title {
    font-size: 2.6rem;
    line-height: 3.9rem;
  }
}
@media (max-width: 767px) {
  .advantages__title {
    font-size: 2.4rem;
    line-height: 3.6rem;
    margin-bottom: 30px;
  }
}

.advantages__image {
  display: flex;
  align-items: center;
  margin-bottom: 18px;
}

@media (max-width: 1365px) {
  .advantages__image--checkmarks {
    width: 27px;
    height: 26px;
  }
}

@media (max-width: 1365px) {
  .advantages__image--cruiser {
    width: 21px;
    height: 26px;
  }
}

@media (max-width: 1365px) {
  .advantages__image--zodiac {
    width: 36px;
    height: 26px;
  }
}

.advantages__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-flow: row;
  gap: 40px;
}
@media (max-width: 1365px) {
  .advantages__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

.advantages__item {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f9fbfd;
  padding: 30px 30px 45px 30px;
}
@media (max-width: 1365px) {
  .advantages__item {
    padding: 26px 16px 20px 20px;
  }
}

@media (max-width: 1365px) {
  .advantages__item:last-of-type .advantages__item-text {
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .advantages__item:last-of-type .advantages__item-text {
    margin-top: 0;
  }
}

.advantages__item-title {
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 2.7rem;
  margin-top: 0;
  margin-bottom: 10px;
}
@media (max-width: 1365px) {
  .advantages__item-title {
    line-height: 2.5rem;
  }
}
@media (max-width: 767px) {
  .advantages__item-title {
    width: 175px;
  }
}

.advantages__item-text {
  font-size: 1.4rem;
  line-height: 2rem;
  margin: 0;
}

.booking {
  position: relative;
  padding: 50px 0 50px 0;
}
@media (max-width: 1365px) {
  .booking {
    padding: 87px 0 100px 0;
  }
}
@media (max-width: 767px) {
  .booking {
    padding-top: 58px;
    padding-bottom: 65px;
  }
}

.booking__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (max-width: 1365px) {
  .booking__container {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}

.booking__text-container {
  align-self: center;
  width: 47%;
  margin-left: 60px;
  padding-bottom: 9px;
}
@media (max-width: 1365px) {
  .booking__text-container {
    width: auto;
    margin-bottom: 52px;
    margin-left: 0;
    padding-bottom: 0;
  }
}
@media (max-width: 767px) {
  .booking__text-container {
    margin-bottom: 35px;
  }
}

.booking__title {
  font-weight: 700;
  font-size: 4rem;
  line-height: 6rem;
  margin-top: 0;
  margin-bottom: 8px;
}
@media (max-width: 1365px) {
  .booking__title {
    font-size: 3.4rem;
    line-height: 5.1rem;
    margin-bottom: 5px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .booking__title {
    font-size: 2.4rem;
    line-height: 3.1rem;
    margin-bottom: 14px;
  }
}

.booking__text {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 0;
  font-size: 1.8rem;
  line-height: 2.7rem;
  max-width: 360px;
}
@media (max-width: 1365px) {
  .booking__text {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .booking__text {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
}

.booking__form-container {
  background: #011c40;
  color: #f9fbfd;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
  padding: 43px 50px 50px 50px;
  width: 35%;
  margin-right: 60px;
}
@media (max-width: 1365px) {
  .booking__form-container {
    width: auto;
    max-width: 403px;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .booking__form-container {
    padding: 34px 30px 40px 30px;
  }
}

.booking__form-title {
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 2.7rem;
  margin-top: 0;
  margin-bottom: 28px;
}
@media (max-width: 767px) {
  .booking__form-title {
    text-align: center;
    line-height: 2.5rem;
  }
}

.booking__submit-button {
  margin-top: 30px;
  min-height: 43px;
}
@media (max-width: 767px) {
  .booking__submit-button {
    margin-top: 27px;
  }
}

.cruises {
  padding-top: 84px;
  padding-bottom: 120px;
  background-color: #f9fbfd;
}
@media (max-width: 1365px) {
  .cruises {
    padding-top: 90px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .cruises {
    padding-top: 56px;
    padding-bottom: 65px;
  }
}

.cruises__title {
  color: #011c40;
  font-weight: 600;
  font-size: 2.8rem;
  line-height: 4.2rem;
  text-align: center;
  margin-top: 0;
  margin-bottom: 50px;
}
@media (max-width: 1365px) {
  .cruises__title {
    font-size: 2.6rem;
    line-height: 3.9rem;
    margin-bottom: 49px;
  }
}
@media (max-width: 767px) {
  .cruises__title {
    margin-bottom: 31px;
    font-size: 2.4rem;
    line-height: 3.6rem;
  }
}

.cruises__card {
  min-height: 386px;
  max-height: 386px;
  width: 260px;
  position: relative;
}
@media (hover: hover) {
  .cruises__card:hover:not(.focus-visible) .cruises__inner-card {
    opacity: 1;
    visibility: visible;
  }
  .cruises__card:hover:not(.focus-visible) .cruises__outer-card {
    opacity: 0;
